<template>
<div class="select">
  <div class="button">
    <el-button type="primary" round v-on:click="addRooms"> + 新增房间</el-button>
  </div>
  <template v-for="(room,index) in rooms">
  <div class="room-info-card" key="index">
    <div class="room-name">
      <div class="flex2">
        <h1>{{roomPrefix}} {{room.roomid}}</h1>
      </div>
      <div class="flex3">
        <el-input-number :min="1" size="mini" v-model="room.area" controls-position="right"></el-input-number>
      </div>
      <div class="flex1 unit">
        <p>㎡</p>
      </div>
    </div>
    <div class="check-box">
      <div class="checklist">
        <el-checkbox v-model="room.sun">有夕照</el-checkbox>
      </div>
      <div class="checklist">
        <el-checkbox v-model="room.top">是顶楼</el-checkbox>
      </div>
    </div>
    <div class="delete-btn" v-on:click="removeRooms(index)">
      <el-button plain size="mini"><i class="el-icon-delete"></i></el-button>
    </div>
  </div>
</template>
  <transition name="el-fade-in">
    <div class="button" v-if="rooms.length!=0">
      <!-- <el-button @click="open2" type="primary" round>计算</el-button> -->
      <el-button plain @click="chooseAlgorithm">生成方案</el-button>
    </div>
  </transition>
  <!-- <div class="all-price" key="price" v-if="resultShow">
    总价：{{allPrice}}
  </div> -->
<transition-group name="el-fade-in">
  <div class="result-container" key="resultContainer"  v-if="resultShow&&rooms.length!=0">
    <div class="result-title">
      <h1>{{ductTypeAlgorithm?"美的家用中央空调风管机方案":"美的家用中央空调小多联方案"}}</h1>
    </div>
    <div class="change-actype" v-on:click="changeAlgorithm">
       <el-button plain size="mini">点击切换{{ductTypeAlgorithm?"小多联":"风管机"}}方案</el-button>
    </div>
  <template v-for="(roominfo,index) in resultsList" >
<div class="roominfo-container" key="container">
  <div class="roominfo" key="room">
    {{roominfo.roomname}}
  </div>
  <div class="acinfo" v-for="(ac,index) in roominfo.ac" key="ac">
    {{ac.model}}, {{ac.price}}
  </div>
  <div class="room-total-price" key="totalPrice">
    房间空调总价: {{roominfo.totalPrice}}
  </div>
</div>
</template>
<template class="outdoor-ac-contaienr" v-if="!ductTypeAlgorithm">
  <div class="outdoor-ac-title">
    室外机
  </div>
  <div class="acw-info" v-for="(acw,index) in mjzw">
    {{acw.model}}, {{acw.price}}
  </div>
  <div class="room-total-price">
    室外机总价: {{mjzwSum}}
  </div>
</template>
  <div class="all-price" key="price" v-if="resultShow">
    设备总价：{{ductTypeAlgorithm?allPrice:(allPrice+mjzwSum)}}
  </div>
  <div class="additional-price" v-if="!ductTypeAlgorithm">
    <span>施工材料:</span> {{materialPrice}}
    <div class="additional-price-notice">
      <p>(该费用包含管，辅料费用，并由厂家收取)</p>
    </div>
  </div>
  <div class="final-price">
    总价：{{finalPrice}}
    <div class="welcome-to-shop">
      <p>详情欢迎到店咨询</p>
    </div>
  </div>
  <div class="link-to-shop">
    <router-link :to="setPath('/point')"">
      附近门店 <i class="el-icon-location-outline"></i>
    </router-link>
  </div>
  </div>


</transition-group>
<transition name="el-fade-in">
<div class="scoll-top-btn" v-on:click="goPageTop" v-if="toTop">
  <el-button size="mini" icon="el-icon-caret-top"></el-button>
</div>
</transition>

</div>
</template>

<script>
// 导入轮播图组件
import {
  swiper,
  swiperSlide
} from 'vue-awesome-swiper'
// 导入vuex
var ac = require("../../static/acData.json");
import {
  mapState,
  mapMutations,
  mapActions
} from 'vuex'
import axios from 'axios'
export default {
  name: 'select',
  data() {
    return {
      num: 1,
      sunChecked: false,
      topChecked: false,
      rooms: [],
      newRoom: "Room",
      counter: 0,
      valSum: 0,
      topConstant: 25,
      sunConstant: 25,
      basicColdCapacity: 220,
      acdata: {},
      resultsList: [],
      roomAcs: [],
      allPrice: 0,
      resultShow: false,
      roomPrefix: "房间",
      toTop: false,
      lsum:0,
      mjzw:[],
      mjzwSum:0,
      finalPrice:0,
      roomAcws:[],
      materialPrice:1000,
      ductTypeAlgorithm:true,
    }
  },
  computed: {
    /**
     * @desc 从store中引入需要的数据
     */
    ...mapState({

    })
  },
  mounted: function() {
    this.$nextTick(function() {
      window.addEventListener('scroll', this.needToTop); //滚动事件监听
    });
    console.log(ac);
  },
  created: function() {
    // var self = this;
    // axios.get('/goods').then(function(res) {
    //   self.acdata = res["data"];
    //   console.log(self.acdata);
    // });
    // console.log(self.acdata);
    var self = this;
    self.acdata = ac;
    console.log(self.acdata, "acdata");
  },
  methods: {
    ...mapActions([
      'askNews', 'setSrc', "test", 'getAcList'
    ]),
    ...mapMutations([
      'loadMore'
    ]),
    loadAcList() {
      var self = this;
      axios.get('/goods').then(function(res) {
        self.acdata = res["data"];
      });
      // console.log(self.acdata);
      alert(self.acdata);
    },
    /**
     * @desc 回到顶部
     */
    goPageTop() {
      // document.body.scrollTop = 0;
      // console.log("top");
      scrollTo(0, 0);
    },
    chooseAlgorithm(){
      let that = this;
      this.finalPrice = 0;
      this.ductTypeAlgorithm ? that.calculate() : that.calculateMjz();
    },
    changeAlgorithm(){
      let that = this;
      this.ductTypeAlgorithm = !this.ductTypeAlgorithm;
      this.finalPrice = 0;
      this.ductTypeAlgorithm ? that.calculate() : that.calculateMjz()
    },
    needToTop() {
      let curHeight = document.documentElement.scrollTop || document.body.scrollTop;
      let viewHeight = document.documentElement.clientHeight;
      if (curHeight > viewHeight - 500) {
        this.toTop = true; //赋值是为了按钮的v-show='toTop'
      } else {
        this.toTop = false;
      }
      // if (stop) { //STOP
      //   // clearInterval(timer); //C2
      //   // console.log('我没了');
      // }
      // stop = true; //B
      // console.log('执行了一次');
    },
    goPageBottom() {
      // document.body.scrollBottom = 0;
      scrollTo(1500, 1500);
    },
    setPath(path) {
      return path;
    },
    addRooms(event) {
      this.resultsList = [];
      event.preventDefault();
      this.counter++;
      this.rooms.push({
        roomid: this.counter,
        sun: false,
        top: false,
        area: 0
      })
    },

    removeRooms(index) {
      let _room = this.rooms;
      // remove
      _room.splice(index, 1)
      if (_room.length == 0) {
        this.counter = 0;
      }
      this.resultShow = false;
    },
    calculate() {
      let _rooms = this.rooms,
        areaArr = [],
        _sunConstant = this.sunConstant,
        _topConstant = this.topConstant,
        _basicColdCapacity = this.basicColdCapacity,
        bp2GRList = this.acdata.acData.bp2GR,
        tmpResultList = [],
        tmpPrice = [],
        allPriceArr = [],
        that = this;
      // console.log(bp2GRList);
      _rooms.forEach(function(room, index) {
        let l = room.area * (_basicColdCapacity + _sunConstant * room.sun + _topConstant * room.top) / 100;
        // console.log(that.digui(l,bp2GRList))
        // 递归结果
        let diguiResult = that.bp2GrRecursion(l, bp2GRList);
        let sum = 0;
        console.log(diguiResult);
        diguiResult.forEach(function(ac, index) {
          tmpPrice.push(ac.price);
        });
        console.log(sum, "sum");
        tmpResultList.push({
            "roomname": that.roomPrefix + room.roomid,
            "ac": diguiResult,
            "totalPrice": that.sum(tmpPrice),
          })
          //重置roomAcs
        that.roomAcs = [];
        tmpPrice = [];
        console.log(l, "l");
      });
      // console.log(that.digui(100,bp2GRList));
      console.log(tmpResultList);
      tmpResultList.forEach(function(ac, index) {
        allPriceArr.push(ac.totalPrice);
      });
      this.resultsList = tmpResultList;
      this.allPrice = this.sum(allPriceArr);
      this.finalPrice = this.allPrice;
      this.resultShow = true;
      this.goPageBottom();
    },
    calculateMjz() {
      let _room = this.rooms,
        areaArr = [],
        _sunConstant = this.sunConstant,
        _topConstant = this.topConstant,
        _basicColdCapacity = this.basicColdCapacity,
        bp3mjzt = this.acdata.acData.bp3mjzt,
        bp3mjzw = this.acdata.acData.bp3mjzw,
        tmpResultList = [],
        tmpPrice = [],
        allPriceArr = [],
        lsumArr = [],
        mjzwPriceArr = [],//储存外机计算总价
        that = this;

        _room.forEach(function(room,index){
          let l = room.area * (_basicColdCapacity + _sunConstant * room.sun + _topConstant * room.top) / 100;
          // console.log(that.digui(l,bp2GRList))
          // 递归结果
          let diguiResult = that.bp3mjztRecursion(l, bp3mjzt);
          let sum = 0;
          lsumArr.push(l);
          console.log(diguiResult)
          diguiResult.forEach(function(ac,index){
            tmpPrice.push(ac.price);
          });
          console.log(sum, "sum");
          tmpResultList.push({
              "roomname": that.roomPrefix + room.roomid,
              "ac": diguiResult,
              "totalPrice": that.sum(tmpPrice),
            })
            //重置roomAcs
          that.roomAcs = [];
          tmpPrice = [];
          console.log(l, "l");
        });
        console.log("mjz");
        console.log(tmpResultList);
        tmpResultList.forEach(function(ac, index) {
          allPriceArr.push(ac.totalPrice);
        });
        // console.log(this.bp3mjzwRecursion(this.lsum,bp3mjzw));
        this.resultsList = tmpResultList;
        this.allPrice = this.sum(allPriceArr);
        this.lsum = this.sum(lsumArr)/1.3;
        this.resultShow = true;
        this.goPageBottom();
        console.log(this.lsum,"lsum");
        let recurseResult = that.bp3mjzwRecursion(this.lsum,bp3mjzw);
        console.log(recurseResult);
        recurseResult.forEach(function(item,index){
          mjzwPriceArr.push(item.price);
        });
        this.mjzw = recurseResult;
        this.mjzwSum = this.sum(mjzwPriceArr);
        this.finalPrice = this.allPrice + this.mjzwSum + this.materialPrice;
        this.roomAcws = [];
        // console.log(recurseResult,"外机结果");
        // console.log(tmpResultList,"暂时结果");

        // console.log(that.bp3mjzwRecursion(200,bp3mjzw))

        // console.log(bp3mjzw);
        // console.log(this.calculateMjzW(this.lsum,bp3mjzw));
        // let tmp = this.calculateMjzW(this.lsum, bp3mjzw);
        // console.log(tmp);
        // console.log(this.calculateMjzW(100,bp3mjzw));
    },
    bp2GrRecursion(l, acs) {
      let tmpAcList = this.roomAcs;
      let tmpAcs = acs;
      if (l > 72) {
        let m = l - 72;
        acs.forEach(function(ac, index) {
          if (ac.p == 72 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
        if (m > 0) {
          this.bp2GrRecursion(m, acs);
        } else {
          // this.resultsList = [];
          return;
        }
      } else if (l > 0 && l <= 26) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 26 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (l > 26 && l <= 35) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 35 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (l > 35 && l <= 51) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 51 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (l > 51 && l <= 72) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 72 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      }
      // console.log(tmpAcList);
      return tmpAcList;
    },
    bp3mjztRecursion(l, acs) {
      let tmpAcList = this.roomAcs;
      let tmpAcs = acs;
      if (l > 71) {
        let m = l - 71;
        acs.forEach(function(ac, index) {
          if (ac.p == 71 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
        if (m > 0) {
          this.bp3mjztRecursion(m, acs);
        } else {
          // this.resultsList = [];
          return;
        }
      } else if (l > 0 && l <= 22) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 22 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (l > 22 && l <= 28) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 28 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (l > 28 && l <= 36) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 36 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (l > 36 && l <= 45) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 45 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (l > 45 && l <= 50) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 50 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (l > 50 && l <= 56) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 56 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (l > 56 && l <= 71) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 71 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      }
      // console.log(tmpAcList);
      return tmpAcList;
    },
    bp3mjzwRecursion(lsum,acs) {
      let tmpAcList = this.roomAcws;
      let tmpAcs = acs;
      if (lsum > 180) {
        let m = lsum - 180;
        acs.forEach(function(ac, index) {
          if (ac.p == 180 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
        if (m > 0) {
          this.bp3mjzwRecursion(m, acs);
        } else {
          // this.resultsList = [];
          return;
        }
        // alert('房型过于复杂,该方案仅供参考');
      } else if (lsum > 0 && lsum <= 80) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 80 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (lsum > 80 && lsum <= 100) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 100 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (lsum > 100 && lsum <= 120) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 120 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (lsum > 120 && lsum <= 140) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 140 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (lsum > 140 && lsum <= 160) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 160 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      } else if (lsum > 160 && lsum <= 180) {
        tmpAcs.forEach(function(ac, index) {
          if (ac.p == 180 && ac.ptc == false) {
            tmpAcList.push({
              "model": ac.model,
              "price": ac.price,
            })
          }
        });
      }
      // console.log(tmpAcList);
      return tmpAcList;
    },
    sum(arr) {
      let s = 0;
      for (var i = arr.length - 1; i >= 0; i--) {
        s += arr[i];
      }
      return s;
    },

  },
  components: {

  }
}
</script>

