<template>
  <header class="head">
    <a href="javascript: void(0)"><img class="midea-logo" src="../../assets/head/midea2.jpg"/></a>
    <span>家用中央空调选型助手</span>
  </header>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
export default {
  name: 'head',
  data () {
    return {
      imgs: [],
      searchContent: ''
    }
  },
  computed: {

  },
  watch: {

  },
  methods: {
    setClass (classname) {
      return classname
    },
    setSrc (src) {
      return src
    },
    searchNewsBtn () {
      alert('搜索完成')
    },
    goNews () {
      this.closeSearch()
      this.clearSearchContent()
    },
    clearSearchContent () {
      this.searchContent = ''
    },
    ...mapMutations([
      'toggleMenu', 'openSearch', 'closeSearch'
    ])
  }
}
</script>

<style lang="stylus">
.head{
  height: 70px;
  background: #fff;
  position: relative;
  border-bottom:1px solid;
  border-bottom-color:rgba(216, 232, 255, 1);
  img{
    height: 50px;
    position: absolute;
    top: 5px;
    right: 20%;
    cursor: pointer;
  }
  .midea-logo{
    height: 60px;
    top: 5px;
    left: 10px;
  }
  span{
    position:absolute;
    top:35px;
    left:170px;
    font-size:4vw;
    font-weight:bold;
    color:rgba(22, 138, 221, 1);
  }
}
</style>



