<template>
<div class="container">
  <div class="link">
    <!-- <a href="https://www.baidu.com/">附近门店</a> -->
  </div>
  <div id="container" class="map-container">

  </div>
  <div id="result" class="map-search-result">

  </div>
</div>
</template>

<script>
import {
  mapState
} from 'vuex'
import AMap from 'AMap'
var map;
var PlaceSearch;
var geolocation;
export default {
  name: 'point',
  data() {
    return {
      map: "",
      center:[118.11022, 24.490474],
    }
  },
  computed: {
    ...mapState({
      points: state => state.PointStore.points
    })
  },
  created: function() {
    // this.init();
    // this.search();
  },
  mounted: function() {
    this.init();
    // this.search();
  },
  methods: {
    init() {
      let that = this;
      map = new AMap.Map('container', {
        center: this.center,
        resizeEnable: true,
        // zoom: 10
      })
      AMap.plugin(['AMap.ToolBar', 'AMap.Scale', 'AMap.Geolocation'], function() {
        map.addControl(new AMap.ToolBar());
        map.addControl(new AMap.Scale());
        geolocation = new AMap.Geolocation({
          enableHighAccuracy: true, //是否使用高精度定位，默认:true
          timeout: 10000, //超过10秒后停止定位，默认：无穷大
          maximumAge: 0, //定位结果缓存0毫秒，默认：0
          convert: true, //自动偏移坐标，偏移后的坐标为高德坐标，默认：true
          showButton: true, //显示定位按钮，默认：true
          buttonPosition: 'LB', //定位按钮停靠位置，默认：'LB'，左下角
          buttonOffset: new AMap.Pixel(10, 20), //定位按钮与设置的停靠位置的偏移量，默认：Pixel(10, 20)
          showMarker: true, //定位成功后在定位到的位置显示点标记，默认：true
          showCircle: true, //定位成功后用圆圈表示定位精度范围，默认：true
          panToLocation: true, //定位成功后将定位到的位置作为地图中心点，默认：true
          zoomToAccuracy: true //定位成功后调整地图视野范围使定位位置及精度范围视野内可见，默认：false
        });
        map.addControl(geolocation);
        geolocation.getCityInfo(function(status,result){
          console.log(result);
          that.center = result.center;
        });
        AMap.event.addListener(geolocation, 'complete', function(result){
          console.log(result,"result");
        });//返回定位信息
        AMap.event.addListener(geolocation, 'error', function(error){
          console.log()
        });      //返回定位出错信息
      });
      AMap.service(["AMap.PlaceSearch"], function() {
        PlaceSearch = new AMap.PlaceSearch({
          pageSize: 2,
          pageIndex: 0,
          city: "0592", //城市
          map: map,
          panel: "result"
        });
        PlaceSearch.search('国美电器龙门|苏宁易购财富', function(status, result) {
          console.log(status);
          console.log(result);
        });
      });
    },
    search() {

    }
  }
}
</script>

<style lang="stylus">
.container{
  display:flex;
  flex-direction:column;
  justify-content:center;
  align-items:center;
  width:100%;
  height:600px;
  .map-container{
    /*border:1px solid black;*/
    width:90%;
    /*height:40%;*/
    height:300px;
    /*flex:1;*/
  }
  .map-search-result{
    flex:2;
    width:90%;
    /*border-radius:12px;*/
    height:300px;
    margin-top:10px;
  }
}
</style>



